
/**
 * XSS 대비 서버에서 변환된 값이(ex:&lt;) 들어오면 클라이언트 화면에서 vue는 outerHTML방식으로 그대로보여줌
 * 그래서 다시 변환해서 보여주는 함수.
 * 그냥 공통 함수임
 * @param {*} str 
 * @returns 
 */
const changeXSS =(str) => {
    return str.replaceAll('&lt;','<').replaceAll('&gt;', '>').replaceAll('&#40;', '(').replaceAll('&#41;', ')');
}
export {changeXSS}
