<script setup>
import { computed } from 'vue';

  const props = defineProps({
    nowPage: Number,
    totalCnt: Number,
    pagePerCnt: Number,
    viewPagingCnt: Number
  })
  console.log(props);
  const emit = defineEmits(["pagingCallback"]);
  //값이 변경되면 계산
  const totalPageCnt = computed(() => (Math.ceil(props.totalCnt / props.pagePerCnt)) );

  const showPagingCnt = computed(()=> {
    let tempTotalPageCnt = Math.ceil(props.totalCnt / props.pagePerCnt);
    if(tempTotalPageCnt == 0) return tempTotalPageCnt;

    let tempShowPagingCnt = 5;
    if(props.viewPagingCnt) {
      tempShowPagingCnt = props.viewPagingCnt;
    }

    if( props.nowPage + tempShowPagingCnt -1 > tempTotalPageCnt ) {
      tempShowPagingCnt = tempTotalPageCnt - props.nowPage +1;
    }
    return tempShowPagingCnt;
  });

  function inquiry(param) {
    emit('pagingCallback', param);
  }
</script>

<template>
  <div class="pagination">
    <a v-if="nowPage>1" href="#" @click="inquiry(1)"><span> 처음 </span></a>
    <a v-if="nowPage>=2" href="#" @click="inquiry(nowPage-1)"><span> 이전 </span></a>
    <template v-for="n in showPagingCnt" :key="n">
    <strong v-if="n+nowPage-1==nowPage" class="active"> {{n+nowPage-1}} </strong>
    <a v-if="n+nowPage-1!=nowPage" href="#" @click="inquiry(n+nowPage-1)"> {{n+nowPage-1}} </a>
    </template>
    <a v-if="nowPage < totalPageCnt" href="#" @click="inquiry(nowPage+1)"><span> 다음 </span></a>
    <a v-if="nowPage < totalPageCnt" href="#" @click="inquiry(totalPageCnt)"><span> 마지막 </span></a>
  </div>
</template>
  
<style>
.pagination {
  display: inline-block;
}

.pagination a,strong {
  color: black;
  float: left;
  padding: 8px 16px;
  text-decoration: none;
}

.pagination strong.active {
  background-color: #4CAF50;
  color: white;
}

.pagination a:hover:not(.active) {background-color: #ddd;}
</style>