<script setup>
import BaseHeader from '@/components/common/header/baseHeader.vue'
import BaseFooter from '@/components/common/footer/baseFooter.vue'
import PagingDiv from '../../components/common/paging/pagingDiv.vue'

import { useStore } from "vuex";
//composition API에서 script setup에서는 tihs에 접근이 불가함. 고로 아래와 같이 선언해야 router 사용 가능
import { useRouter } from 'vue-router'
import axios from "axios";
import { ref, onBeforeMount} from 'vue';

import { AgGridVue } from 'ag-grid-vue3'; // Vue3 AgGrid Component

import 'ag-grid-community/styles/ag-grid.css' // 그리드 기본 css
import 'ag-grid-community/styles/ag-theme-alpine.css' // 그리드 테마

import { changeXSS } from '../../utils/XSSutil'

  onBeforeMount(() => {
    getBoardList(1);
  });

  const store = useStore();
  const router = useRouter();

  const rowData = ref([]);
  const resultTotalCnt = ref(0);//페이징. 전체 건수
  const nowPage = ref(1);//페이징. 현재 페이지

  const srcTitle = ref('');

  const getBoardList = async (param) => {
    store.commit("changeLoadingStatus", true);
    nowPage.value = param;
    await axios.post("/boardList.do",{nowPage:param,title:srcTitle.value})
    .then((res) => {
      console.log('조회결과',res.data.RESULT_LIST);
      if(res.data.RESULT_SIZE == 0) {
        rowData.value = [];
        resultTotalCnt.value = 0;
      } else {
        rowData.value = res.data.RESULT_LIST;

        res.data.RESULT_LIST.map(function(row) {
          row.BODY_TEXT = changeXSS(row.BODY_TEXT);
          row.TITLE = changeXSS(row.TITLE);
          return row;
        });
        resultTotalCnt.value = res.data.RESULT_TOTAL_CNT;
      }
    })
    .catch((res) => {
      console.error("에러", res);
      rowData.value = [];
//      return 'ERROR'
    });
    store.commit("changeLoadingStatus", false);
  }

  const colDefs = ref([
    { field: "CATEGORY",headerName: '카테고리', checkboxSelection: true, width: 120 },
    { field: "CODE_NAME", headerName: '카테고리명', width: 110},
    { field: "TITLE" ,headerName: '제목',editable: true, width: 140 },
    { field: "BODY_TEXT", headerName: '내용', width: 150 },
    { field: "COMMENT_CNT", headerName: '댓글수', width: 80 },
    { field: "REG_DT2",headerName: '등록일',editable: true, cellEditor: 'agDateStringCellEditor',
        cellEditorParams: {
            min: '2020-01-01',
            max: '2027-12-31',
        }, width: 120 }
  ]);

  let gridObject = {};
  const onMountGrid =(prop) =>{
    console.log(prop);
    gridObject = prop.api;
  }

  const onCellClicked = (e) => {
    console.log(e.colDef.field);
    if( e.colDef.field == 'CATEGORY' || e.colDef.field == 'CODE_NAME' 
//    || e.colDef.field == 'BODY_TEXT'
  ) {
      router.push('/boardDetail/'+e.data.SEQ);
    }
  }

  const saveBoardData =() =>{
/*    gridObject.startEditingCell({
      rowIndex: 0,
      colKey: 'TITLE'
    });
*/
    // eslint-disable-next-line no-debugger
//위 주석을 써야 이 debugger가 먹힌다. 전체적으로 풀수도 있으나 나쁜 케이스가 발생 할 수 있다.    debugger;

    console.log(gridObject.getSelectedNodes());
  }

  const deleteBoardData=()=> {
    if(gridObject.getSelectedNodes().length==0) {
      alert('삭제할 값을 선택해');
      return false;
    } else if (gridObject.getSelectedNodes().length > 1) {
      alert('삭제할 값을 한개만 선택해');
      return false;
    }

    if(gridObject.getSelectedNodes()[0].data.COMMENT_CNT != null) {
      alert('댓글이 있어서 삭제할수 없어');
      return false;
    }


    let seq = gridObject.getSelectedNodes()[0].data.SEQ;

    console.log(gridObject.getSelectedNodes());

    store.commit("changeLoadingStatus", true);
    axios.post("/deleteBoardOne.do",{SEQ:seq})
    .then((res) => {
      console.log('조회결과',res.data);
      if(res.data.RESCODE == '0000') {
        alert('삭제되었습니다.');
        getBoardList();
      } else {
        alert(res.data.RESMSG);
        return false;
      }
    })
    .catch((res) => {
      console.error("에러", res);
      alert(res);
    });
    store.commit("changeLoadingStatus", false);

  }

  const moveCreate = () => {
    router.push('/boardCreate');//history stack에 쌓지 않는다고 나와있는데 현재 페이지가 이 페이지로 전환되는것일뿐. 
  }

  const pagingCallback = (param) => {
    console.log(param);
    getBoardList(param);
  }

</script>

<template>
  <BaseHeader title="게시판"></BaseHeader>
  <div>
    <main>
    <div>
      <span>게시판 리스트 조회</span><span style="float:right">총 {{resultTotalCnt}}건</span>
    </div>
    <div class="divRightAlign">
      <input v-model="srcTitle" placeholder="제목입력" @keyup.enter="getBoardList(1)" />
      <button type="button" id="searchBtn" @click="getBoardList(1)">조회</button>
      <button type="button" id="searchBtn" @click="moveCreate">글쓰기</button>
      <button type="button" id="searchBtn" @click="saveBoardData">저장</button><br>
      <button type="button" id="searchBtn" @click="deleteBoardData">삭제</button><br>
    </div>
    <div>
      <ag-grid-vue 
        style="height: 480px" 
        class="ag-theme-alpine" 
        :rowData="rowData" 
        :columnDefs="colDefs" 
        :localeText="{noRowsToShow: `조회 결과가 없습니다`}" 
        rowSelection="multiple"
        singleClickEdit = true 
        @grid-ready="onMountGrid"
        @cellClicked="onCellClicked"
      >
      </ag-grid-vue>
      <PagingDiv :nowPage="nowPage" :totalCnt="resultTotalCnt" :pagePerCnt=10 :viewPagingCnt=5 @pagingCallback="pagingCallback"/>
    </div>
  </main>
</div>
<BaseFooter></BaseFooter>
</template>

